html {
  font-size: 16px;
  font-family: "Roboto Condensed", arial, sans-serif;
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a {
  text-decoration: none;
  transition: 0.3s; }
  a:hover {
    opacity: 0.8; }
  a:active {
    filter: blur(1px); }

img {
  max-width: 100%; }

.container {
  margin: 0 auto;
  max-width: 1160px; }
  @media screen and (max-width: 1199px) {
    .container {
      max-width: 678px; } }
  @media screen and (max-width: 767px) {
    .container {
      max-width: 290px; } }

.hero {
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  padding: 30px 0 47px 0; }
  @media screen and (max-width: 1199px) {
    .hero {
      padding: 40px 0 78px 0; } }
  @media screen and (max-width: 767px) {
    .hero {
      padding: 20px 0 80px 0; } }

.hero__main {
  display: flex;
  justify-content: space-between; }

.hero__welcome {
