  align-items: flex-end;
  justify-content: flex-end;
  width: 34.3%;
  color: #ffffff;
  margin-bottom: 46px; }
  @media screen and (max-width: 1199px) {
    .hero__welcome {
      width: 48%;
      margin-bottom: 27px; } }
  @media screen and (max-width: 767px) {
    .hero__welcome {
      width: 100%;
      margin-bottom: 0; } }
  .hero__welcome h3 {
    font-size: 49px;
    font-family: "Bebas Neue", tahoma, sans-serif;
    font-weight: 400;
    letter-spacing: 0.06rem;
    text-transform: uppercase;
    margin-bottom: 10px; }
    @media screen and (max-width: 1199px) {
      .hero__welcome h3 {
        font-size: 34px;
        margin-bottom: 5px; } }
    @media screen and (max-width: 767px) {
      .hero__welcome h3 {
        font-size: 28px;
        margin-bottom: 10px; } }
  .hero__welcome a {
    display: inline-flex;
    padding: 20px 54px;
    border: 4px solid #ffffff;
    font-size: 12px;
    font-weight: 700;
    letter-spacing: 0.05em;
    color: #ffffff;
    text-transform: uppercase; }
    @media screen and (max-width: 1199px) {
      .hero__welcome a {
        padding: 20px 30px; } }
    @media screen and (max-width: 767px) {
      .hero__welcome a {
        padding: 20px 82px; } }

.hero__title {
  position: relative;
  margin-bottom: 57px;
  text-align: right; }
  @media screen and (max-width: 1199px) {
    .hero__title {
      margin-bottom: 39px; } }
  @media screen and (max-width: 767px) {
    .hero__title {
      margin-bottom: 41px; } }
  .hero__title h1 {
    margin: 0;
    font-size: 109px;
    font-family: "Bebas Neue", tahoma, sans-serif;
    font-weight: 700;
    line-height: 109px;
    text-transform: uppercase;
    letter-spacing: 0.2rem; }
    @media screen and (max-width: 1199px) {
      .hero__title h1 {
        font-size: 86px;
        line-height: 86px; } }
    @media screen and (max-width: 767px) {
      .hero__title h1 {
        font-style: 78px;
        line-height: 78px;
        letter-spacing: 0; } }
  .hero__title ul {
    position: absolute;
    bottom: 25px;
    left: 20px;
    list-style: none;
    margin: 0;
    padding: 0;
    font-size: 14px; }
    @media screen and (max-width: 1199px) {
      .hero__title ul {
        bottom: 15px;
        left: 0; } }
    @media screen and (max-width: 767px) {
      .hero__title ul {
        bottom: 10px; } }
    .hero__title ul li {
      padding: 3px 0; }

.hero__banner {
  width: 53.5%; }
  @media screen and (max-width: 1199px) {
    .hero__banner {
      width: 48%; } }
  @media screen and (max-width: 767px) {
    .hero__banner {
      display: none; } }
  @media screen and (max-width: 1199px) {
    .hero__banner img {
      width: 324px;
      height: 390px; } }

.hero__socials {
  position: relative;
  margin-bottom: 43px;
  width: 1%; }
  @media screen and (max-width: 1199px) {
    .hero__socials {
      display: none; } }
  .hero__socials ul {
    position: absolute;
    bottom: 0;
    list-style: none;
    margin: 0;
    padding: 0; }
  .hero__socials li {
    margin-bottom: 15px; }
    .hero__socials li:last-child {
      margin-bottom: 0; }
  .hero__socials a {
    font-size: 12px;
    text-transform: uppercase;
    color: #ffffff; }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 0.8; }
  @media screen and (max-width: 1199px) {
    .header {
      margin-bottom: 21px; } }
  @media screen and (max-width: 767px) {
    .header {
      margin-bottom: 47px; } }
  .header a {
    cursor: pointer; }
    @media screen and (max-width: 767px) {
      .header a svg {
        width: 68px;
        height: 15px; } }
  .header a:last-child {
    font-family: "Bebas Neue", tahoma, sans-serif;
    font-size: 26px;
    color: #ffffff;
    letter-spacing: 0.06rem; }
    @media screen and (max-width: 1199px) {
      .header a:last-child {
